import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ShareDealsComponent } from './share-deals.component';


const routes: Routes = [
  {
    path: '',
    component: ShareDealsComponent,
    data: {
      title: 'Shared Deals'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ShareDealsRoutingModule { }
