import { Component, ViewChild, ElementRef, OnDestroy, OnInit } from '@angular/core';
import { RequirementService } from '../../_services/requirement.service';
import { ShareDealData } from '../../_models/ShareDealData';
import { AlertifyService } from '../../_services/alertify.service';
import { Subject, Subscription } from 'rxjs';
declare var $;

@Component({
  selector: 'app-share-deals',
  templateUrl: './share-deals.component.html',
  styleUrls: ['./share-deals.component.css']
})
export class ShareDealsComponent implements OnInit {
  dtOptions: DataTables.Settings = {
    pagingType: 'full_numbers',
    pageLength: 25,
    retrieve: true,
    order: [[0, 'desc']]
};
  dtTrigger: Subject<ShareDealData> = new Subject();
  shareddatalists: ShareDealData[];
  roleid: any =  Number(String(localStorage.getItem('RoleID')));

  constructor(private requirementservice: RequirementService, private alertify: AlertifyService) { }

  ngOnInit() {
    this.loadSharedData();
  }
  loadSharedData() {
    return this.requirementservice.loadSharedData().subscribe((shareddatalists: ShareDealData[]) => {
      console.log(shareddatalists);
      this.shareddatalists = shareddatalists.filter(proj => (Number(proj.flag) === 0));
      this.dtTrigger.next();
    }, error => {
      this.alertify.error(error);
    });

  }

}
